import React from "react";
import { useParams } from "react-router-dom";

import { Preloader } from "./Preloader";
import { useAppDispatch, useAppSelector } from "../../hooks";
import { toggleShoppingBagItem } from "../../redux/shoppingBag/slice";
import { selectShoppingBag } from "../../redux/shoppingBag/selectors";
import { selectWishList } from "../../redux/wishList/selectors";
import { toggleWishListItem } from "../../redux/wishList/slice";
import { selectFullProduct } from "../../redux/fullProduct/selectors";
import { fetchFullProduct } from "../../redux/fullProduct/asyncActions";

import s from "./FullProductInfo.module.scss";

const FullProduct: React.FC = () => {
  const { items: bagItems } = useAppSelector(selectShoppingBag);
  const { items: wishList } = useAppSelector(selectWishList);
  const { fullProductItem, status } = useAppSelector(selectFullProduct);
  const dispatch = useAppDispatch();

  const { id } = useParams();
  const isAddedToBag = bagItems.some((obj) => obj.id === id);
  const isAddedToWishList = wishList.some((obj) => obj.id === id);

  React.useEffect(() => {
    id && dispatch(fetchFullProduct({ id }));
  }, [id]);
  const handleToggleWhishListItem = () => {
    dispatch(toggleWishListItem(fullProductItem));
  };
  const handleToggleBagItem = () => {
    dispatch(toggleShoppingBagItem(fullProductItem));
  };

  const FullProductElem = (
    <div className={s.page}>
      <div className={`${s.container} container`}>
        <div className={s.image}>
          <img src={fullProductItem?.imageUrl} alt={fullProductItem.title} />
        </div>
        <div className={s.wrapp}>
          <h2 className={s.title}>{fullProductItem?.title}</h2>
          <div className={s.price}>€ 150</div>
          <div className={s.buttons}>
            <button onClick={handleToggleBagItem} className={s.addToBagButton}>
              {isAddedToBag ? "ADDED TO BAG" : "ADD TO BAG"}
            </button>
            <button
              onClick={handleToggleWhishListItem}
              className={s.likeButton}
            >
              <svg
                width="44"
                height="39"
                viewBox="0 0 44 39"
                fill={isAddedToWishList ? "#000" : "none"}
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M21.8864 37C21.8864 37 2 25.8636 2 12.3409C2 9.95036 2.82826 7.63363 4.34385 5.7849C5.85944 3.93618 7.96874 2.66966 10.3129 2.20083C12.657 1.732 15.0912 2.08982 17.2013 3.21342C19.3113 4.33702 20.9669 6.15697 21.8864 8.36365C22.8058 6.15697 24.4614 4.33702 26.5715 3.21342C28.6815 2.08982 31.1157 1.732 33.4598 2.20083C35.804 2.66966 37.9133 3.93618 39.4289 5.7849C40.9445 7.63363 41.7727 9.95036 41.7727 12.3409C41.7727 25.8636 21.8864 37 21.8864 37Z"
                  stroke="black"
                  strokeOpacity="0.75"
                  strokeWidth="4"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );

  return <>{status === "loading" ? <Preloader /> : FullProductElem}</>;
};

export default FullProduct;
