import React, { useRef } from "react";
import Typed from "typed.js";

import s from "./FullProductInfo.module.scss";

import heart from "../../assets/img/heartIcon.svg";
import SVGPreloader from "../../assets/img/productImagePreloader.svg";

export const Preloader: React.FC = () => {
  const titleSpanRef = useRef<HTMLSpanElement>(null);
  const priceSpanRef = useRef<HTMLSpanElement>(null);

  React.useEffect(() => {
    const titleTyped = new Typed(titleSpanRef.current, {
      strings: ["..."],
      typeSpeed: 100,
      cursorChar: "",
      loop: true,
    });
    const priceTyped = new Typed(priceSpanRef.current, {
      strings: ["..."],
      typeSpeed: 100,
      cursorChar: "",
      loop: true,
    });
    return () => {
      titleTyped.destroy();
      priceTyped.destroy();
    };
  }, []);

  return (
    <div className={s.page}>
      <div className={`${s.container} container`}>
        <div className={`${s.image} ${s.productImagePreloader}`}>
          <img src={SVGPreloader} alt="Preloader" />
        </div>
        <div className={s.wrapp}>
          <h2 className={s.title}>
            Loading <span ref={titleSpanRef}></span>
          </h2>
          <div className={s.price}>
            € <span ref={priceSpanRef}></span>
          </div>
          <div className={s.buttons}>
            <button className={`${s.addToBagButton}`}>ADD TO BAG</button>
            <button className={`${s.likeButton}`}>
              <img width={40} height={35} src={heart} alt="Preloader" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
